import React, { createContext, useState, useCallback, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { HttpUtils } from "../utils/httputils";

const initialAuth = {};
const AuthContext = createContext(initialAuth);

const AuthProvider = ({ children }) => {
  const navigate = useNavigate();
  const [datosUsuario, setDatosUsuario] = useState({});

  useEffect(()=>{
    const fetchData = async () => {
      var pathActual = window.location.href;
      const urlObj = new URL(pathActual);
      const path = urlObj.pathname;
      try{
       

        if(localStorage.getItem("token")){
          const usuario = await HttpUtils.GET("http://localhost:8080/api/verificarToken");
          usuario.data.token = "";
          setDatosUsuario(usuario.data);
  
  
          if(path == "/login"){
            navigate("/home");
          }
        }else{

          if(path != "/registrar"){
            navigate("/login");

          }


        }
      }catch{
        if(path != "/registrar"){
          navigate("/login");

        }
      }
    };
    console.log(datosUsuario);
    fetchData();

   
  },[])


  const data = {
    datosUsuario, setDatosUsuario
  };
  return <AuthContext.Provider value={data}>{children}</AuthContext.Provider>;
};

export { AuthProvider };
export default AuthContext;
