import React, { useState } from 'react';
/* Fotos */
import zuki from "../media/zuki.png";
import fondo from "../media/fondo/fondiu.png";
import farriba from "../media/fondo/arriba.png";
import fabajo from "../media/fondo/abajo.png";
import fizq from "../media/fondo/izquierda.png";
import fder from "../media/fondo/derecha.png";
import llenizq from "../media/fondo/izquierda-lleno.png";
import llender from "../media/fondo/derecha-lleno.png";
import binocular from "../media/icons/misiones.png";
import patita from "../media/icons/patita.png";
import lectura from "../media/icons/lectura.png";
import logout from "../media/icons/logout.svg";
import cuenta from "../media/icons/perfil.png";
import infoi from "../media/icons/info.png";
/* PAGES */
import Zorro from "../components/Zorro";
import Info from "../components/info";
import Misiones from "../components/misiones";
import Lectura from "../components/leer";
import Treserres from "../components/treserres";
import Error from "../components/error";

export function Index() {
  const [MostrarMisiones, setMostrarMisiones] = useState(false);
  const [MostrarZorro, setMostrarZorro] = useState(false);
  const [MostrarLectura, setMostrarLectura] = useState(false);
  const [MostrarInfo, setMostrarInfo] = useState(false);
  const nivel = 10;
  const llevo = 650;
  const total = 1500;
  const treserres = 1;
  const error = 1;
  
  const handleMostrarMisiones = ()=> {
    setMostrarMisiones(false);
  };

  const handleMostrarZorro = ()=> {
    setMostrarZorro(false);
  };

  const handleMostrarLectura = ()=> {
    setMostrarLectura(false);
  };
  
  const handleMostrarInfo = ()=> {
    setMostrarInfo(false);
  };

  const cerrarSesion = () => {
    localStorage.clear();
    window.location.href='/login';
  }


  return (
    <div className="back" style={{ backgroundImage: `url(${fondo})` }}>
      <div className="contenedor dispo-flex center-hor center-ver columna-ver">

      <nav className="navbar-logout">
          <button className="caja-logout">
            <a src="" className="misiones">
              <img src={logout} className="icon-nav" style={{margin: "3px"}} onClick={cerrarSesion} />
            </a>
          </button>
        </nav>
        {/* MARGENES */}
        <div className="arriba">
          <img src={farriba} className="hojarriba" />
        </div>
        <div className="abajo">
          <img src={fabajo} className="hojabajo" />
        </div>
        <div className="izquierda">
          <img src={fizq} className="hojaizq" />
        </div>
        <div className="derecha">
          <img src={fder} className="hojader" />
        </div>
        <div className="lleno-izquierda">
          <img src={llenizq} className="hojas-izquierda" />
        </div>
        <div className="lleno-derecha">
          <img src={llender} className="hojas-derecha" />
        </div>
        
        {<div className="contenido">
            {MostrarMisiones && <Misiones handleMostrarMisiones={handleMostrarMisiones} />}
          </div>}

        {<div className="contenido">
            {MostrarZorro && <Zorro handleMostrarZorro={handleMostrarZorro} />}
          </div>}

        {<div className="contenido">
            {MostrarLectura && <Lectura handleMostrarLectura={handleMostrarLectura} />}
          </div>}

        {<div className="contenido">
            {MostrarInfo && <Info handleMostrarInfo={handleMostrarInfo} />}
          </div>}


        {treserres > 2 ? <Treserres /> : ""}
        {error > 2 ? <Error /> : ""}

        {/* CONTENIDO */}
        <div className="titulo dispo-flex center-hor center-ver">
          <h1 className="">Zuki</h1>
        </div>
        <div className="mascota dispo-flex center-hor center-ver">
          <img
            src={zuki}
            className="wp-200"
            alt="Dibujo caraturizado de un zorro chilote con gorro de lana."
          />
        </div>
        <div className="barra">
          <div className="nivel">
            <h3 className="nivel-text">Nivel:</h3>
            <h3 className="nuvel-num">{nivel}</h3>
          </div>
          <div className="barra-todo">
            <div className="barra-nivel"></div>
          </div>
          <h4 className="experiencia">
            {llevo}/{total}
          </h4>
        </div>
        <nav className="navbar zindex1">
          <div className="caja">
          <a button onClick={() => setMostrarMisiones(!MostrarMisiones)} className="binoculares">
              {MostrarMisiones ? ` ` : ` `}
              <img src={binocular} className="icon-nav" />
            </a>
            <a button onClick={() => setMostrarZorro(!MostrarZorro)} className="patita">
              {MostrarZorro ? ` ` : ` `}
              <img src={patita} className="icon-nav" />
            </a>
            <a button onClick={() => setMostrarLectura(!MostrarLectura)} className="lectura">
              {MostrarLectura ? ` ` : ` `}
              <img src={lectura} className="icon-nav" />
            </a>
            <a button onClick={() => setMostrarInfo(!MostrarInfo)} className="info">
              {MostrarInfo ? ` ` : ` `}
              <img src={infoi} className="icon-nav" />
            </a>
          </div>
        </nav>

      </div>
    </div >
  );
}
