import { useContext, useEffect, useState } from "react";
import loginFondo from "../media/fondo.png";
import { useFormik } from "formik";
import * as Yup from "yup";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import Swal from "sweetalert2";
import AuthContext from "../Context/auth.context";

export function Crear() {
  const navigate = useNavigate();
  const { datosUsuario, setDatosUsuario } = useContext(AuthContext);
  const [tiposDeMascotas, setTiposDeMascotas] = useState([]);

  const formik = useFormik({
    initialValues: {
      nombre: "",
      correo: "",
      clave: "",
      nombreMascota: "",
      tipoMascotaId: -1,
    },
    validationSchema: Yup.object().shape({
      nombre: Yup.string().nullable(true).required("El campo es obligatorio"),
      correo: Yup.string()
        .nullable(true)
        .email("Correo Invalido")
        .required("El campo es obligatorio"),
      clave: Yup.string().nullable(true).required("El campo es obligatorio"),
      nombreMascota: Yup.string()
        .nullable(true)
        .required("El campo es obligatorio"),
      tipoMascotaId: Yup.number()
        .nullable(true)
        .min(1, "El campo es obligatorio")
        .required("El campo es obligatorio"),
    }),
    validateOnMount: true,
    onSubmit: async (values) => {
      try {
        var usuario = await axios.post(
          "http://localhost:8080/api/usuario",
          values
        );
        Swal.fire("¡ Felicidades !", "Ya puedes comenzar a jugar", "success");
        localStorage.setItem("token", usuario.data.token);
        usuario.data.token = "";
        setDatosUsuario(usuario.data);
        navigate("/home")

      } catch (error) {
        if (error.response) {
          // Si hay una respuesta del servidor con un código de estado
          if (error.response.status === 400) {
            // Si el código de estado es 400 (BadRequest)
            Swal.fire("Error", error.response.data, "error");
            // Muestra el mensaje de error proporcionado por el servidor
          } else {
            // Manejo de otros códigos de estado aquí, si es necesario
            Swal.fire("Error", "Ocurrió un error en la solicitud.", "error");
          }
        } else if (error.request) {
          // Si no hay respuesta del servidor, puede ser un problema de conexión
          Swal.fire("Error", "No se pudo conectar al servidor.", "error");
        } else {
          // Otros tipos de errores
          Swal.fire("Error", "Ocurrió un error inesperado.", "error");
        }
      }
    },
  });

  useEffect(() => {
    const fetchData = async () => {
      await ObtenerTiposDeMascotas();
    };
    console.log(datosUsuario);
    fetchData();
  }, []);

  const ObtenerTiposDeMascotas = async () => {
    var mascotas = await axios.get("http://localhost:8080/api/tipoMascota");
    setTiposDeMascotas(
      mascotas.data.map((x) => {
        return {
          label: x.tipoMascota,
          value: x.tipoMascotaId,
          ...x,
        };
      })
    );
  };

  return (
    <div
      className="imagendefondo"
      style={{ backgroundImage: `url(${loginFondo})` }}
    >
      <div className="wrapper">
        <form onSubmit={formik.handleSubmit}>
          <h1>Crear usuario</h1>
          <div className="input-box">
            <input
              type="text"
              placeholder="Nombre de usuario"
              required
              {...formik?.getFieldProps("nombre")}
            />
            <i className="bx bxs-user"></i>
            {formik?.touched["nombre"] && formik?.errors["nombre"] && (
              <div className="text-red-500 mt-1 ml-2">
                {formik?.errors["nombre"]}
              </div>
            )}
          </div>
          <div className="input-box">
            <input
              type="text"
              placeholder="Correo electrónico"
              required
              {...formik?.getFieldProps("correo")}
            />
            <i className="bx bxs-envelope"></i>
            {formik?.touched["correo"] && formik?.errors["correo"] && (
              <div className="text-red-500 mt-1 ml-2">
                {formik?.errors["correo"]}
              </div>
            )}
          </div>
          <div className="input-box">
            <input
              type="password"
              placeholder="Contraseña"
              required
              {...formik?.getFieldProps("clave")}
            />
            <i className="bx bxs-lock-alt"></i>
            {formik?.touched["clave"] && formik?.errors["clave"] && (
              <div className="text-red-500 mt-1 ml-2">
                {formik?.errors["clave"]}
              </div>
            )}
          </div>
          <div className="input-box">
            <input
              type="text"
              placeholder="Nombre mascota"
              required
              {...formik?.getFieldProps("nombreMascota")}
            />
            <i className="bx bxs-lock-alt"></i>
            {formik?.touched["nombreMascota"] &&
              formik?.errors["nombreMascota"] && (
                <div className="text-red-500 mt-1 ml-2">
                  {formik?.errors["nombreMascota"]}
                </div>
              )}
          </div>
          <div className="input-box">
            <select
              {...formik?.getFieldProps("tipoMascotaId")}
              options={tiposDeMascotas}
            >
              <option disabled value={-1}>
                {"Seleccione una Mascota"}
              </option>
              {tiposDeMascotas.map((x, index) => {
                return (
                  <option key={index} value={x.value}>
                    {x.label}
                  </option>
                );
              })}
            </select>
            <i className="bx bxs-lock-alt"></i>
            {formik?.touched["tipoMascotaId"] &&
              formik?.errors["tipoMascotaId"] && (
                <div className="text-red-500 mt-1 ml-2">
                  {formik?.errors["tipoMascotaId"]}
                </div>
              )}
          </div>

          <button type="submit" className="btn" disabled={!formik.isValid}>
            Crear Usuario
          </button>

          <div className="register-link">
            <p>
              <Link to="/" className="nodecotext">
                Volver al inicio
              </Link>
            </p>
          </div>
        </form>
        <h3 className="outside-rectangle">Lorem ipsum dolor sit amet conse.</h3>
      </div>
    </div>
  );
}
