import React, { useState } from 'react';
import "./style.css";
import fotozorro from "./zorrochilote.jpg";

export default function Zorro({ handleMostrarZorro }) {
  const [MostrarTreserres, setMostrarTreserres] = useState(true);
  const handleMostrarTreserres = () => {
    setMostrarTreserres(false);
  };
  return (
    <div className="container-2 cont-zorro">
  <h1 className="titulo">Zorro Chilote</h1>
  <p className="parrafo">
    El zorro chilote es un cánido endémico de Chile que habita en bosques
    nativos valdivianos, pastizales y terrenos abiertos. Se encuentra
    principalmente en la Isla Grande de Chiloé y en la Cordillera de
    Nahuelbuta.
    <br />
    es una especie importante para el ecosistema, ya que ayuda a controlar
    las poblaciones de roedores y otros animales. La conservación de este
    animal es fundamental para el equilibrio de los ecosistemas del sur de
    Chile.
  </p>
  <img className="imagen fotito" src={fotozorro} alt="Zorro Chilote" />

  <div>
    <a button onClick={() => handleMostrarZorro()} className="info">
      <div className='close'>
        x
      </div>
    </a>
  </div>
</div>
  );
}
