import loginFondo from "../media/fondo.png";
import { useFormik } from "formik";
import * as Yup from "yup";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import Swal from "sweetalert2";
import { useContext } from "react";
import AuthContext from "../Context/auth.context";
export function Login() {
  const navigate = useNavigate();
  const {setDatosUsuario } = useContext(AuthContext);

  const formik = useFormik({
    initialValues: {
       correo: "",
       clave: ""
    },
    validationSchema: Yup.object().shape({
        correo: Yup.string()
            .nullable(true)
            .email("Correo Invalido")
            .required("El campo es obligatorio"),
        clave: Yup.string()
            .nullable(true)
            .required("El campo es obligatorio"),
    }),
    validateOnMount: true,
    onSubmit: async (values) => {
        try{
            var usuario = await axios.post("http://localhost:8080/api/login", values);
            localStorage.setItem("token", usuario.data.token);
            usuario.data.token = "";
            setDatosUsuario(usuario.data);
            navigate("/home")

        }catch (e){
            Swal.fire("Error en inicio de sesión",e.response.data, "error")
        }
    }
});

  return (
    <div
      className="imagendefondo"
      style={{ backgroundImage: `url(${loginFondo})` }}
    >
      <div className="wrapper">
        <form onSubmit={formik.handleSubmit}>
          <h1>Inicio de Sesión</h1>
          <div className="input-box">
            <input type="text" placeholder="Correo" required
            {...formik?.getFieldProps("correo")}
            />
            <i className="bx bxs-user"></i>
            {formik?.touched["correo"] && formik?.errors["correo"] && (
          <div className="text-red-500 mt-1 ml-2">{formik?.errors["correo"]}</div>
        )}
          </div>
          <div className="input-box">
            <input type="password" placeholder="Contraseña" required
            {...formik?.getFieldProps("clave")}
            />
            <i className="bx bxs-lock-alt"></i>
            {formik?.touched["clave"] && formik?.errors["clave"] && (
          <div className="text-red-500 mt-1 ml-2">{formik?.errors["clave"]}</div>
        )}
          </div>

          <button type="submit" className="btn" disabled={!formik.isValid}>
            Iniciar sesión
          </button>

          <div className="register-link">
            <p>
                <Link to="/registrar"  className="nodecotext">¿No tienes cuenta? Registrate</Link>
            </p>
          </div>
        </form>
        <h3 className="outside-rectangle">Lorem ipsum dolor sit amet conse.</h3>
      </div>
    </div>
  );
}
