import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom";

import { Login } from "./pages/login";
import { Index } from "./pages/index";
import { Crear } from "./pages/crear";
import { AuthProvider } from "./Context/auth.context";
import { Storycuatro } from "./pages/storycuatro";
import { Storyuno } from "./pages/storyuno";
import { Storydos } from "./pages/storydos";
import { Storytres } from "./pages/storytres";
import { Treserres } from "./pages/treserres";
import { Error } from "./pages/error";


export function Rutas() {
  return (
    <BrowserRouter>
      <AuthProvider>
        <Routes>
        <Route
            path=""
            element={<Navigate to="/login" replace />}
          />
          <Route path="/login" element={<Login />}></Route>
          <Route path="/registrar" element={<Crear />}></Route>          
          <Route path="/home" element={<Index />}></Route>
          <Route path="/storyuno" element={<Storyuno />}></Route>
          <Route path="/storydos" element={<Storydos />}></Route>
          <Route path="/storytres" element={<Storytres />}></Route>
          <Route path="/storycuatro" element={<Storycuatro />}></Route>
          <Route path="/treserres" element={<Treserres />}></Route>
{          <Route path="/error" element={<Error />}></Route>}
        </Routes>
      </AuthProvider>
    </BrowserRouter>
  );
}
